"use client";
import { useState, useEffect } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { Plus, Search, Trash2 } from "lucide-react";
import theme from "@/styles/theme";

/**
 * Admin Student Management Page
 * Consumes: theme.colors.primary, accent, surfaceElevated
 */
export default function AdminStudentsPage() {
  const [students, setStudents] = useState<any[]>([]);
  const [search, setSearch] = useState("");
  const [form, setForm] = useState({
    name: "",
    matricNo: "",
    email: "",
    department: "",
    level: "",
  });
  const [loading, setLoading] = useState(false);
  const token = typeof window !== "undefined" ? localStorage.getItem("token") : null;

  // 🔹 Fetch all students
  const fetchStudents = async () => {
    try {
      const res = await fetch("/api/students", {
        headers: { Authorization: `Bearer ${token}` },
      });
      const data = await res.json();
      setStudents(data || []);
    } catch (err) {
      console.error(err);
      setStudents([]);
    }
  };

  useEffect(() => {
    fetchStudents();
  }, []);

  // 🔹 Add new student
  const handleAddStudent = async () => {
    if (!form.name || !form.matricNo || !form.email || !form.department || !form.level)
      return alert("Please fill in all required fields.");

    setLoading(true);
    try {
      const res = await fetch("/api/students", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(form),
      });
      const data = await res.json();
      if (!res.ok) throw new Error(data.message || "Failed");
      alert("Student added successfully!");
      setForm({ name: "", matricNo: "", email: "", department: "", level: "" });
      fetchStudents();
    } catch (err: any) {
      console.error(err);
      alert("Error adding student: " + err.message);
    } finally {
      setLoading(false);
    }
  };

  // 🔹 Delete student
  const handleDelete = async (id: string) => {
    if (!confirm("Are you sure you want to delete this student?")) return;
    setLoading(true);
    try {
      const res = await fetch(`/api/students/${id}`, {
        method: "DELETE",
        headers: { Authorization: `Bearer ${token}` },
      });
      if (!res.ok) throw new Error("Failed");
      alert("Student deleted successfully.");
      fetchStudents();
    } catch (err) {
      console.error(err);
      alert("Error deleting student.");
    } finally {
      setLoading(false);
    }
  };

  // 🔹 Filter
  const filtered = students.filter(
    (s) =>
      s.name.toLowerCase().includes(search.toLowerCase()) ||
      s.email.toLowerCase().includes(search.toLowerCase()) ||
      s.matricNo.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <main className="max-w-6xl mx-auto p-6 min-h-screen">
      <h1 className="text-2xl font-bold text-primary mb-6">Manage Students</h1>

      {/* Add new student */}
      <Card className="mb-6 rounded-xl shadow-md">
        <CardContent className="p-4">
          <h2 className="text-lg font-semibold mb-4">Add New Student</h2>
          <div className="grid sm:grid-cols-2 md:grid-cols-3 gap-4 mb-4">
            <div>
              <Label>Full Name</Label>
              <Input
                value={form.name}
                onChange={(e) => setForm({ ...form, name: e.target.value })}
                placeholder="e.g. Muna ❤️"
              />
            </div>
            <div>
              <Label>Matric No</Label>
              <Input
                value={form.matricNo}
                onChange={(e) => setForm({ ...form, matricNo: e.target.value })}
                placeholder="e.g. CSC/21/001"
              />
            </div>
            <div>
              <Label>Email</Label>
              <Input
                type="email"
                value={form.email}
                onChange={(e) => setForm({ ...form, email: e.target.value })}
                placeholder="e.g. muna@afued.edu.ng"
              />
            </div>
            <div>
              <Label>Department</Label>
              <Input
                value={form.department}
                onChange={(e) => setForm({ ...form, department: e.target.value })}
                placeholder="e.g. Computer Science"
              />
            </div>
            <div>
              <Label>Level</Label>
              <Input
                value={form.level}
                onChange={(e) => setForm({ ...form, level: e.target.value })}
                placeholder="e.g. 300"
              />
            </div>
          </div>

          <Button
            onClick={handleAddStudent}
            disabled={loading}
            className="bg-primary text-on-primary min-h-[2.5rem]"
          >
            <Plus size={16} className="mr-2" /> {loading ? "Adding..." : "Add Student"}
          </Button>
        </CardContent>
      </Card>

      {/* Student list */}
      <Card className="rounded-xl shadow-md">
        <CardContent className="p-4">
          <div className="flex flex-col sm:flex-row justify-between mb-4 gap-3">
            <h2 className="text-lg font-semibold">All Students</h2>
            <div className="relative">
              <Search className="absolute left-3 top-2.5 h-4 w-4 text-gray-400" />
              <Input
                className="pl-9"
                placeholder="Search students..."
                value={search}
                onChange={(e) => setSearch(e.target.value)}
              />
            </div>
          </div>

          {filtered.length === 0 ? (
            <p className="text-gray-500 text-sm">No students found.</p>
          ) : (
            <div className="overflow-x-auto">
              <table className="min-w-full border text-sm">
                <thead className="bg-surfaceElevated border-b">
                  <tr className="text-left">
                    <th className="p-2">Name</th>
                    <th className="p-2">Matric No</th>
                    <th className="p-2">Email</th>
                    <th className="p-2">Department</th>
                    <th className="p-2">Level</th>
                    <th className="p-2 text-center">Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {filtered.map((student, idx) => (
                    <tr key={student._id || idx} className="border-b hover:bg-gray-50 transition">
                      <td className="p-2">{student.name}</td>
                      <td className="p-2">{student.matricNo}</td>
                      <td className="p-2">{student.email}</td>
                      <td className="p-2">{student.department}</td>
                      <td className="p-2">{student.level}</td>
                      <td className="p-2 text-center">
                        <button
                          className="inline-flex items-center px-2 py-1 text-xs border border-red-400 text-red-500 rounded-md"
                          onClick={() => handleDelete(student._id)}
                        >
                          <Trash2 size={14} className="mr-1" /> Delete
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </CardContent>
      </Card>
    </main>
  );
}
